.admin-dashboard {
    transition: var(--transition);
}

.admin-dashboard.opensidebar {
    transform: translateX(100%);
}

.admin-dashboard .admin-dashboard-container {
    background: #F4F6F6;
}

.admin-dashboard .container {
    margin: 0 auto;
    width: 100%;
    max-width: 1300px;
    padding-bottom: 25px;
}

.infos-content {
    display: flex;
    gap: 25px 15px;
    flex-direction: column;
}

@media (min-width: 992px) {
    .infos-content {
        flex-direction: row;
    }
    .infos-content > :first-child {
        flex: .8;
    }
    
    .infos-content > :last-child {
        flex: .2;
    }
}

@media (min-width: 1200px) {
    .admin-dashboard, .admin-dashboard.opensidebar {
        width: calc(100% - 300px);
        transform: translateX(300px);
        z-index: 200;
        position: relative;
        width: calc(100% - 55px);
        transform: translateX(55px);
    }
    .admin-dashboard.openadmindashboard {
        width: calc(100% - 300px);
        transform: translateX(300px);
    }
}