.recent-orders-section {
    background: #fff;
    padding: 15px;
}

.recent-order-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.view-all-recent-orders-link {
    color: #fff;
    background: var(--maincolor);
    padding: 8px 10px;
    display: inline-block;
    cursor: pointer;
    border-radius: 5px;
    margin-left: 8px;
}

.recent-orders {
    overflow: auto;
}

.recent-orders-title h4 {
    text-transform: capitalize;
    font-weight: 700;
    font-size: 1.1rem;
}

.recent-orders-items {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.recent-orders-items > * {
    min-width: 150px;
    width: 100%;
}

.recent-orders-items > :nth-child(2),
.recent-orders-items > :nth-child(3) {
    min-width: 100px;
    width: 80%;
    text-align: center;
}

.recent-orders-items > :last-child {
    text-align: right;
}

.recent-orders-items-infos:not(:last-child) {
    border-bottom: 1px solid #EAEDED;
}

.recent-orders-items-infos {
    padding: 8px 4px;
    cursor: context-menu;
}

.recent-orders-items-infos:hover {
    background: var(--maincolor);
}

.recent-orders-items-infos:hover > * {
    color: #fff;
}

.recent-orders-items-infos > * {
    font-weight: 600;
}

.status > * {
    padding: 2px;
    border-radius: 2px;
    color: #fff;
}

.status.green > *{
    background: greenyellow;
}

.status.red > * {
    background: #E74C3C;
}

.status.blue > *{
    background: var(--maincolor);
}

.status.yellow > * {
    background: #F5B041;
}

.recent-orders::-webkit-scrollbar {
    height: 15px;
  }

.recent-orders::-webkit-scrollbar-track {
    border-radius: 10px;
    border: 1px solid var(--maincolor);
  }
   
  /* Handle */
  .recent-orders::-webkit-scrollbar-thumb {
      cursor: pointer;
    background: var(--maincolor); 
    border-radius: 10px;
  }