.admin-dashboard__sidebar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #154360;
    transform: translateX(-100%);
    transition: var(--transition);
}

.admin-dashboard__sidebar.opensidebar {
    transform: translateX(0);
}

.sidebar__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    margin-bottom: 20px;
}

.sidebar__header, .sidebar__link li > * {
    padding: 12px 20px;
}

.sidebar__link li:hover {
    background: var(--maincolor);
}

.sidebar__link li > * {
    display: flex;
    align-items: center;
    color: #fff;
    width: 100%;
    height: 100%;
}

.sidebar__link li > * > :first-child {
    font-size: 1.5rem;
    margin-right: 15px;
}

.header__close {
    width: 23px;
    height: 23px;
    position: relative;
    cursor: pointer;
}

.header__close > * {
    width: 100%;
    height: 4px;
    position: absolute;
    top: 10px;
    background: #fff;
}

.header__close > :first-child {
    transform: rotate(45deg);
}

.header__close > :last-child {
    transform: rotate(-45deg);
}

.header_logo {
    display: flex;
    align-items: center;
}

.logo__icon > * {
    font-size: 2rem;
    margin-right: 5px;
}

@media (min-width: 1200px) {
    .admin-dashboard__sidebar {
        width: 300px;
        transform: translateX(0);
    }
    .header__close {
        display: none;
    }
}

