.admin-dashboard__navbar {
    padding: 10px;
    background: #fff;
}

.admin-navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar__bars > *{
    cursor: pointer;
    font-size: 1.5rem;
}

.navbar__search {
    position: relative;
    width: 100%;
    max-width: 300px;
    height: 37px;
    margin: 0 10px;
}

.navbar__search input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    outline: none;
    border: 1px solid #000;
    border-radius: 30px;
    padding: 0 25px 0 35px;
}

.navbar__search input::placeholder {
    font-weight: bold;
}

.search-icon {
    position: absolute;
    top: 50%;
    left: 10px;
    font-size: 1.2rem;
    transform: translateY(-50%);
    font-weight: bold;
}

.navbar__user-icon > * {
    font-size: 2rem;
}