.business-information {
    padding: 25px 0;
    display: grid;
    gap: 25px;
}

.buisness-information__card {
    background: #fff;
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.content__value {
    font-weight: 700;
    font-size: 1.8rem;
}

.content__info {
    color: #BFC9CA;
    font-size: 14px;
    font-weight: 700;
}

.content__icon > * {
    font-size: 2rem;
    color: var(--maincolor);
}

@media (min-width: 768px) {
    .business-information {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (min-width: 1200px) {
    .business-information {
        grid-template-columns: repeat(4, 1fr);
    }
}