@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap');

:root {
  --maincolor: #5DADE2;
  --transition: .5s;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
}

#root {
  overflow-x: hidden;
}

.container {
  padding: 0 25px;
}

ul {
  list-style: none;
}

.title {
  font-size: 18px;
}

a {
  text-decoration: none;
}