@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap);
:root {
  --maincolor: #5DADE2;
  --transition: .5s;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
}

#root {
  overflow-x: hidden;
}

.container {
  padding: 0 25px;
}

ul {
  list-style: none;
}

.title {
  font-size: 18px;
}

a {
  text-decoration: none;
}
.admin-dashboard__navbar {
    padding: 10px;
    background: #fff;
}

.admin-navbar {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.navbar__bars > *{
    cursor: pointer;
    font-size: 1.5rem;
}

.navbar__search {
    position: relative;
    width: 100%;
    max-width: 300px;
    height: 37px;
    margin: 0 10px;
}

.navbar__search input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    outline: none;
    border: 1px solid #000;
    border-radius: 30px;
    padding: 0 25px 0 35px;
}

.navbar__search input::-webkit-input-placeholder {
    font-weight: bold;
}

.navbar__search input:-ms-input-placeholder {
    font-weight: bold;
}

.navbar__search input::placeholder {
    font-weight: bold;
}

.search-icon {
    position: absolute;
    top: 50%;
    left: 10px;
    font-size: 1.2rem;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    font-weight: bold;
}

.navbar__user-icon > * {
    font-size: 2rem;
}
.admin-dashboard {
    transition: var(--transition);
}

.admin-dashboard.opensidebar {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}

.admin-dashboard .admin-dashboard-container {
    background: #F4F6F6;
}

.admin-dashboard .container {
    margin: 0 auto;
    width: 100%;
    max-width: 1300px;
    padding-bottom: 25px;
}

.infos-content {
    display: -webkit-flex;
    display: flex;
    grid-gap: 25px 15px;
    gap: 25px 15px;
    -webkit-flex-direction: column;
            flex-direction: column;
}

@media (min-width: 992px) {
    .infos-content {
        -webkit-flex-direction: row;
                flex-direction: row;
    }
    .infos-content > :first-child {
        -webkit-flex: .8 1;
                flex: .8 1;
    }
    
    .infos-content > :last-child {
        -webkit-flex: .2 1;
                flex: .2 1;
    }
}

@media (min-width: 1200px) {
    .admin-dashboard, .admin-dashboard.opensidebar {
        width: calc(100% - 300px);
        -webkit-transform: translateX(300px);
                transform: translateX(300px);
        z-index: 200;
        position: relative;
        width: calc(100% - 55px);
        -webkit-transform: translateX(55px);
                transform: translateX(55px);
    }
    .admin-dashboard.openadmindashboard {
        width: calc(100% - 300px);
        -webkit-transform: translateX(300px);
                transform: translateX(300px);
    }
}
.business-information {
    padding: 25px 0;
    display: grid;
    grid-gap: 25px;
    gap: 25px;
}

.buisness-information__card {
    background: #fff;
    padding: 15px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.content__value {
    font-weight: 700;
    font-size: 1.8rem;
}

.content__info {
    color: #BFC9CA;
    font-size: 14px;
    font-weight: 700;
}

.content__icon > * {
    font-size: 2rem;
    color: var(--maincolor);
}

@media (min-width: 768px) {
    .business-information {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (min-width: 1200px) {
    .business-information {
        grid-template-columns: repeat(4, 1fr);
    }
}
.recent-custumers {
    background: #fff;
    padding: 15px;
}

.recent-custumers .title {
    margin-bottom: 20px;
}

.recent-customers-item {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 5px 10px;
    cursor: context-menu;
}

.recent-customers-item:hover {
    background: var(--maincolor);
}

.recent-customers-item-icon > * {
    font-size: 1.7rem;
    margin-right: 15px;
    color: var(--maincolor);
}

.recent-customers-item-infos h3 {
    font-size: 16px;
}

.recent-customers-item-infos h5 {
    color: #BFC9CA;
    font-size: 12px;
    letter-spacing: 1px;
}

.recent-customers-item:hover > *, .recent-customers-item:hover .recent-customers-item-icon > * {
    color: #fff;
}
.recent-orders-section {
    background: #fff;
    padding: 15px;
}

.recent-order-header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 20px;
}

.view-all-recent-orders-link {
    color: #fff;
    background: var(--maincolor);
    padding: 8px 10px;
    display: inline-block;
    cursor: pointer;
    border-radius: 5px;
    margin-left: 8px;
}

.recent-orders {
    overflow: auto;
}

.recent-orders-title h4 {
    text-transform: capitalize;
    font-weight: 700;
    font-size: 1.1rem;
}

.recent-orders-items {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.recent-orders-items > * {
    min-width: 150px;
    width: 100%;
}

.recent-orders-items > :nth-child(2),
.recent-orders-items > :nth-child(3) {
    min-width: 100px;
    width: 80%;
    text-align: center;
}

.recent-orders-items > :last-child {
    text-align: right;
}

.recent-orders-items-infos:not(:last-child) {
    border-bottom: 1px solid #EAEDED;
}

.recent-orders-items-infos {
    padding: 8px 4px;
    cursor: context-menu;
}

.recent-orders-items-infos:hover {
    background: var(--maincolor);
}

.recent-orders-items-infos:hover > * {
    color: #fff;
}

.recent-orders-items-infos > * {
    font-weight: 600;
}

.status > * {
    padding: 2px;
    border-radius: 2px;
    color: #fff;
}

.status.green > *{
    background: greenyellow;
}

.status.red > * {
    background: #E74C3C;
}

.status.blue > *{
    background: var(--maincolor);
}

.status.yellow > * {
    background: #F5B041;
}

.recent-orders::-webkit-scrollbar {
    height: 15px;
  }

.recent-orders::-webkit-scrollbar-track {
    border-radius: 10px;
    border: 1px solid var(--maincolor);
  }
   
  /* Handle */
  .recent-orders::-webkit-scrollbar-thumb {
      cursor: pointer;
    background: var(--maincolor); 
    border-radius: 10px;
  }
.admin-dashboard__sidebar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #154360;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: var(--transition);
}

.admin-dashboard__sidebar.opensidebar {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.sidebar__header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    color: #fff;
    margin-bottom: 20px;
}

.sidebar__header, .sidebar__link li > * {
    padding: 12px 20px;
}

.sidebar__link li:hover {
    background: var(--maincolor);
}

.sidebar__link li > * {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    color: #fff;
    width: 100%;
    height: 100%;
}

.sidebar__link li > * > :first-child {
    font-size: 1.5rem;
    margin-right: 15px;
}

.header__close {
    width: 23px;
    height: 23px;
    position: relative;
    cursor: pointer;
}

.header__close > * {
    width: 100%;
    height: 4px;
    position: absolute;
    top: 10px;
    background: #fff;
}

.header__close > :first-child {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.header__close > :last-child {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.header_logo {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.logo__icon > * {
    font-size: 2rem;
    margin-right: 5px;
}

@media (min-width: 1200px) {
    .admin-dashboard__sidebar {
        width: 300px;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    .header__close {
        display: none;
    }
}


